<!-- 水波图 -->

<template>
  <div class="demo">
    <div class="box">
      <h3>水波图</h3>
      <div id="container"></div>
    </div>
    <div class="box">
      <h3>样式自定义的水波图</h3>
      <div id="containerGs"></div>
    </div>
  </div>
</template>

<script>
import { Liquid, measureTextWidth } from '@antv/g2plot'

export default {
  components: {},
  data() {
    return {
      data: []
    }
  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const liquidPlot = new Liquid('container', {
      percent: 0.25,
      outline: {
        border: 4,
        distance: 8
      },
      wave: {
        length: 128
      }
    })
    liquidPlot.render()

    this.containerGs()
  },
  methods: {
    containerGs() {
      const liquidPlot = new Liquid(document.getElementById('containerGs'), {
        percent: 0.26,
        statistic: {
          title: {
            formatter: () => '盈利率',
            style: ({ percent }) => ({
              fill: percent > 0.65 ? 'white' : 'rgba(44,53,66,0.85)'
            })
          },
          content: {
            style: ({ percent }) => ({
              fontSize: 60,
              lineHeight: 1,
              fill: percent > 0.65 ? 'white' : 'rgba(44,53,66,0.85)'
            }),
            customHtml: (container, view, { percent }) => {
              const { width, height } = container.getBoundingClientRect()
              const d = Math.sqrt(Math.pow(width / 2, 2) + Math.pow(height / 2, 2))
              const text = `占比 ${(percent * 100).toFixed(0)}%`
              const textWidth = measureTextWidth(text, { fontSize: 60 })
              const scale = Math.min(d / textWidth, 1)
              return `<div style="width:${d}px;display:flex;align-items:center;justify-content:center;font-size:${scale}em;line-height:${
                scale <= 1 ? 1 : 'inherit'
              }">${text}</div>`
            }
          }
        },
        liquidStyle: ({ percent }) => {
          return {
            fill: percent > 0.45 ? '#5B8FF9' : '#FAAD14',
            stroke: percent > 0.45 ? '#5B8FF9' : '#FAAD14'
          }
        },
        color: () => '#5B8FF9'
      })
      liquidPlot.render()

      let data = 0.25
      const interval = setInterval(() => {
        data += Math.min(Math.random() * 0.1, 0.1)
        if (data < 0.75) {
          liquidPlot.changeData(data)
        } else {
          clearInterval(interval)
        }
      }, 500)
    }
  },
  created() {
    // var wrapperElement = '<div style="position:relative;"></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGs {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGz {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerpt {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGet {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerTexture {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



